.modal {
  position: relative;
  text-align: left;
  background-color: white;
  padding: 5px;
  border-radius: 3px;
  margin: auto;
  display: inline-block;

  width: 280px;
  border-radius: 150px 0px;


  transition-property: transform;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 768px) {
    min-width: 595px;
    border-radius: 300px 0px;
  }

  @media screen and (min-width: 1200px) {
    min-width: 930px;
  }
}

// допоможні діви модального вікна
// ========================================
.wrap-2 {
  display: table;
  width: 100%;
  height: 100%;
  text-align: center;
}

.wrap-3 {
  vertical-align: middle;
  display: table-cell;
}

.wrap-4 {
  margin: 10px;
}
// ========================================

.modal-btn-close {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 25px;
  right: 25px;
  fill: #4c00fe;

  background-color: Transparent;
  border: none;
  cursor: pointer;
  outline: none;
  overflow: hidden;

  transition-property: fill;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  transition-property: transform;
  transition-duration: 400ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  :focus {
    fill: #dc56c5;
    transform: scale(1.3);
  }
}

.modal__wrap {
  display: flex;
  justify-content: center;
}

.modal__big-img-wrap {
  max-width: 40%;
  object-fit: cover;
  @media screen and (max-width: 767px) {
    display: none;
    margin: 15px;
  }
  @media screen and (min-width: 768px) {
    margin: 30px;
  }
}

.modal__small-img {
  box-shadow: 0 0 0 1px #dc56c5, 0 0 20px #333;

  margin: 0 auto;
  margin-top: 40px;
  width: 100px;
  height: 100px;
  border-radius: 50%;

  @media screen and (max-width: 767px) {
    margin-top: 50px;
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 20px;
    width: 80px;
    height: 80px;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 10px;
    width: 150px;
    height: 150px;
  }
}

.modal__content-wrap {
  margin: 15px;
  max-width: 50%;
  @media screen and (max-width: 767px) {
    text-align: center;
  }
  @media screen and (min-width: 1200px) {
    margin: 30px;
  }
}

.modal__title {
  margin-top: 10px;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.2;

  text-transform: uppercase;
  color: #dc56c5;
  text-shadow: 2px 2px 4px #c7bbbb;

  @media screen and (min-width: 1200px) {
    margin-top: 20px;
    font-size: 24px;
  }
}

.modal__title-first {
  margin-top: 0px;
}

.modal__text {
  margin-top: 5px;
  line-height: 1.2;
  color: #0e0e0e;

  @media screen and (min-width: 1200px) {
    margin-top: 7px;
    font-size: 18px;
  }
}

.modal__content-wrap > .modal__btn-buy {
  margin-bottom: 15px;
}

.modal__btn-buy {
  padding-top: 10px;
  width: 145px;
  height: 40px;
  text-align: center;
  text-decoration: none;
  margin-top: 10px;
  font-weight: 500;
  color: #ffffff;

  background-color: #4c00fe;
  box-shadow: 0 0 20px #333;
  border-radius: 5px;
  cursor: pointer;
  display: block;
  transition-property: background-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-property: transform;
  transition-duration: 400ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  :focus {
    background-color: #dc56c5;
    transform: scale(1.1);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  }

  @media screen and (max-width: 767px) {
    margin-left: auto;
    margin-right: auto;
  }

  @media screen and (min-width: 1200px) {
    margin-top: 20px;
  }
}

.modal__btn-author-wrap {
  display: flex;
  justify-content: center;
  margin: 10px 0 114px 0;

  @media screen and (min-width: 768px) {
    margin: 30px 0 56px 0;
  }
}

.modal__btn-author {
  font-weight: 500;
  color: #4c00fe;
  padding: 10px 3px;
  background-color: Transparent;
  box-shadow: 0 0 20px #333;
  border: 1px solid #4c00fe;
  border-radius: 5px;
  cursor: pointer;

  transition-property: background-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-property: transform;
  transition-duration: 400ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  :focus {
    background-color: #dc56c5;
    color: #ffffff;
    border: none;
    transform: scale(1.1);
  }
}

// штрихкод

.barcode::before {
  margin-right: 10px;
  content: '';
  top: 0;
  left: 0;
  display: inline-block;
  width: 24px;
  height: 16px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../images/modal/mobile_ticket.png);

  @media screen and (min-width: 1200px) {
    background-image: url(../images/modal/desctop_ticket.png);
  }
}
